* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  width: 100%;
  background-color: #fff;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  top: 0;
}

#search-form {
  font: inherit;
  width: fit-content;
  flex-direction: row;
  gap: .5em;
  margin: auto;
  padding: 20px;
  display: flex;
}

input {
  width: 100%;
  height: 40px;
  color: #522bc7;
  resize: none;
  background-color: rgba(0, 0, 0, 0);
  border: 2px solid #522bc7;
  border-radius: 7px;
  outline: rgba(0, 0, 0, 0);
  padding-left: 20px;
  font-size: 24px;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin: auto;
  padding: 10px;
  display: flex;
}

.photo-card {
  width: 350px;
  height: auto;
  box-shadow: 1px 5px 5px #c0bfbf;
}

img {
  width: 100%;
  height: 70%;
  margin: 0;
  display: block;
}

.info {
  width: 100%;
  flex-direction: row;
  gap: 10px;
  padding: 10px;
  display: flex;
}

.info-item {
  text-align: center;
  margin: 0;
  padding: 0;
}

form button, button {
  width: 130px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #522bc7;
  border: none;
  border-radius: 7px;
  padding: 10px 15px;
  font-size: 17px;
  font-weight: 300;
  display: block;
}

.load-more {
  margin: 20px auto;
}

button:hover, button:focus {
  color: #000;
  background-color: #6e52c5;
}

